html, body {
  font-family: 'Poppins';
  font-size: 16px;
  font-weight: 400;
  background-color: #1a1b1e;
  transition: all ease .3s;
  max-width: 100%;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  width: 100vw;
  height: auto;
}

.left-side {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: stretch;
  height: auto;
  background-color: #ffffff;
  border-top-left-radius: 1.79vw;
  border-bottom-left-radius: 1.79vw;
  width: 93vw;
}

.cont-nutrition {
  width: 55%;
  padding: 1.04vw 2.6vw 2.6vw 2.6vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.brand-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.right-side {
  background-color: #eaf2f9;
  border-top-left-radius: 1.79vw;
  border-bottom-left-radius: 1.79vw;
  width: 45vw;
  padding: 0.78vw;
  box-shadow: inset 2px 10px 10px 0px rgba(190,203,216,1);
}

.container-logo {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding-right: 0.89vw;
    width: 30%;
}

.logo {
  width: 8.93vw;
  height: auto;
}

/* ============ Media Query ============= */
@media screen and (max-width: 1024px) {
  .left-side {
    width: 90vw;
  }
  .cont-nutrition {
    width: 55%;
    padding: 1.04vw 2.6vw 2.6vw 2.6vw;
  }
  .right-side {
    width: 45vw;
    padding: 0.38vw;
    display: flex;
    flex-direction: column;
    box-shadow: inset 2px 5px 5px 0px rgba(190,203,216,1);
  }
  .container-logo {
    padding-right: 0.89vw;
    width: 40%;
  }

  .logo {
    width: 12.93vw;
    height: auto;
  }
}

@media screen and (max-width: 768px) {
  .left-side {
    width: 88vw;
  }
  .cont-nutrition {
    width: 65%;
    padding: 0vw 1.54vw 1.54vw 1.54vw;
  }
  .brand-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .right-side {
    width: 35vw;
    padding: 0.28vw;
    display: flex;
    flex-direction: column;
    box-shadow: inset 2px 5px 5px 0px rgba(190,203,216,1);
  }
  .container-logo {
    padding-right: 0.89vw;
    width: 40%;
  }

  .logo {
    width: 18.93vw;
    height: auto;
  }
}